import { Link, Route, Routes, useNavigate } from "react-router-dom"
import { useEffect, useState, useMemo, lazy } from 'react';
import './scss/styles.scss'
import SearchBar from './searchBar.js';

const Gown = lazy(() => import("./client/components/gown.component.js"));
const Unknown = lazy(() => import('./client/components/404.component.js'));
const OurContacts = lazy(() => import('./ourContacts'));
const HomeMenu = lazy(() => import("./client/components/home.component.js"));
const ListCatalog = lazy(() => import("./client/components/listCatalog.component.js"));

const AdminHome = lazy(() => import("./admin/component/adminHome.component.js"));
const AddGown = lazy(() => import("./admin/component/add.component.js"));
const EditGown = lazy(() => import("./admin/component/edit.component.js"));

export const SERVERURL = 'https://dsistaluxury.cyclic.app/';

function App() {
	const navigate = useNavigate();

	const [offset, setOffset] = useState(0);
	const [SearchOpen, setSearchOpen] = useState(false);
	const [InputValue, setInputValue] = useState("");

	useEffect(() => {
		const onScroll = () => setOffset(window.pageYOffset);
		window.removeEventListener('scroll', onScroll);
		window.addEventListener('scroll', onScroll, { passive: true });
		return () => window.removeEventListener('scroll', onScroll);
	}, []);

	const title = useMemo(() => {
		return {
			color: offset > 20 ? 'rgba(255, 255, 255, 1)' : 'rgba(0 , 0 , 0 , 1)',
			backgroundColor: offset > 20 ? 'rgba(0, 0, 0, 0.6)' : 'rgba(0, 0, 0, 0)',
			height: '3rem',
			zIndex: '50',
			transition: '0.25s'
		}
	}, [offset])

	const titleText = useMemo(() => {
		return {
			color: offset > 20 ? 'rgba(255, 255, 255, 1)' : 'rgba(0 , 0 , 0 , 1)',
			backgroundColor: 'rgba(0, 0, 0, 0)',
			textDecoration: 'none',
			transition: '0.25s'
		}
	}, [offset])

	const search = useMemo(() => {
		return {
			blockBehind: {
				display: SearchOpen ? 'block' : 'none',
				PointerEvent: SearchOpen ? 'none' : 'auto',
				width: '100vw',
				height: '100vh',
				zIndex: '51'
			},
			mainDiv: {
				aspectRatio: SearchOpen ? 'unset' : 1,
				opacity: SearchOpen ? 1 : 0.75,
				borderRadius: SearchOpen ? '10px' : '50%',
				width: SearchOpen ? '90%' : 'auto',
				left: SearchOpen ? '50%' : 'auto',
				top: SearchOpen ? '50%' : 'auto',
				bottom: SearchOpen ? 'auto' : '0%',
				transform: SearchOpen ? 'translateX(-50%) translateY(-50%)' : 'none',
				padding: SearchOpen ? '0.5rem' : '1rem',
				margin: SearchOpen ? '1rem 0' : '0rem 1rem 1rem 1rem',
				zIndex: '51',
				transition: ' border-radius 0s',
				maxWidth: '50rem'
			},
			button: {
				display: SearchOpen ? 'none' : 'auto'
			},
			input: {
				display: SearchOpen ? 'grid' : 'none'
			}

		}
	}, [SearchOpen])

	const onSubmit = (e) => {
		var kode = InputValue.toUpperCase();
		navigate('./id/' + kode);
		setSearchOpen(false)
	};

	return (
		<div>
			<nav className='container-fluid position-fixed top-0 start-50 translate-middle-x backdrop_blur'
				style={title}>
				<Link to="/" title='Home' className='text-center position-absolute top-50 start-50 translate-middle'
					style={titleText} >D'SISTA LUXURY</Link>
			</nav>

			<div>
				{/* placeHolder */}
				<div style={{ height: '3rem' }} />

				{/* Buat block belakang pas Search screen true */}
				<div className='position-fixed bg-dark bg-opacity-75 top-0' style={search.blockBehind} onClick={() => {
					if (!SearchOpen) return
					setSearchOpen(false);
				}} />

				<SearchBar SearchOpen={SearchOpen} setSearchOpen={setSearchOpen} onSubmit={onSubmit} setInputValue={setInputValue} search={search} />

				<div className='bg-dark bg-opacity-75 position-fixed p-3 rounded-circle aspectRatio_1 overflow-hidden end-0 bottom-0 backdrop_blur cursor_pointer scaleOnActive'
					style={{ minWidth: '2rem', margin: '0 1rem 5rem 0', zIndex: '49' }}
					onClick={() => { window.scrollTo(0, 0); }}>
					<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="white" className="bi bi-arrow-up" viewBox="0 0 16 16">
						<path fillRule="evenodd" d="M8 15a.5.5 0 0 0 .5-.5V2.707l3.146 3.147a.5.5 0 0 0 .708-.708l-4-4a.5.5 0 0 0-.708 0l-4 4a.5.5 0 1 0 .708.708L7.5 2.707V14.5a.5.5 0 0 0 .5.5z" />
					</svg>
				</div>
			</div>

			<Routes>
				{/* global */}
				<Route path='/404' exact element={<Unknown />} />

				{/* client */}
				<Route path='/' exact element={<HomeMenu />} />
				<Route path='/:id' exact element={<ListCatalog />} />
				<Route path='/id/:kode' exact element={<Gown />} />

				{/* admin */}
				<Route path='/admin' exact element={<AdminHome />} />
				<Route path='/admin/edit/:id' exact element={<EditGown />} />
				<Route path='/admin/add' exact element={<AddGown />} />
			</Routes>

			<div className='container-fluid mt-2 overflow-visible bg-light bg-opacity-25 pb-4 bottom-0'>
				<hr />
				<OurContacts />
			</div>
		</div>
	);
}

export default App;
