
export default function SearchBar({ SearchOpen, setSearchOpen, onSubmit, setInputValue, search }) {
    return (
        <div className='position-fixed end-0 overflow-hidden bg-dark backdrop_blur cursor_pointer scaleOnActive ' style={search.mainDiv} onClick={() => {
            if (SearchOpen) return;
            setSearchOpen(true);
        }}>

            <form onSubmit={onSubmit} className='overflow-hidden input-group'>

                <label htmlFor='searchKode' className=' d-flex align-items-center cursor_pointer'>
                    <svg xmlns="http://www.w3.org/2000/svg" fill="white" width="24" height="24" className="bi bi-search" viewBox="0 0 16 16">
                        <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
                    </svg>
                </label>
                <input id='searchKode' className='form-control rounded ms-2' type='text' name='kode' placeholder='Search Dengan Kode Gaun ex.E4' style={search.input} onChange={e => setInputValue(e.target.value)} />

                <button onClick={onSubmit} className='align-items-center bg-dark mx-2 scaleOnActive' style={search.input} type='button'>
                    <svg xmlns="http://www.w3.org/2000/svg" fill="white" width="28" height="28" className="bi bi-check2" viewBox="0 0 16 16">
                        <path d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0z" />
                    </svg>
                </button>

                <button className='align-items-center bg-dark me-1 scaleOnActive' style={search.input} type='button' onClick={() => {
                    if (!SearchOpen) return;
                    setSearchOpen(false);
                }}>
                    <svg xmlns="http://www.w3.org/2000/svg" fill="white" width="24" height="24" className="bi bi-x-lg" viewBox="0 0 16 16">
                        <path d="M2.146 2.854a.5.5 0 1 1 .708-.708L8 7.293l5.146-5.147a.5.5 0 0 1 .708.708L8.707 8l5.147 5.146a.5.5 0 0 1-.708.708L8 8.707l-5.146 5.147a.5.5 0 0 1-.708-.708L7.293 8 2.146 2.854Z" />
                    </svg>
                </button>
            </form>
        </div>
    );
}

